.center {
  font-family: "Papyrus";
  margin: auto;
  width: 50%;
  padding: 0.5em;
}

h1 {
  font-size: 32pt;
  margin: 10px 0px 10px 0px;
}
h2,
h3,
h4 {
  font-size: 24pt;
  font-family: "Papyrus";
  margin: 10px 0px 10px 0px;
}

.a_icon {
  color: black;
}

a {
  color: orange;
  text-decoration: none;
}

p {
  font-family: "Papyrus";
  font-size: 14pt;
  margin: 8px 0px 8px 8px;
}

#title {
  text-align: center;
}

li {
  padding: 0px 15px 0px 15px;
  font-size: 20px;
}

.centerUl {
  color: black;
  display: -ms-inline-flexbox;
  display: inline-flex;
  list-style-type: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

#line {
  margin: 10px;
  color: black;
  border-style: solid;
  border-width: 0.5px;
  padding: 0px;
}

div {
  padding-bottom: 10px;
}

img {
  height: 70%;
  width: 80%;
  -o-object-fit: cover;
     object-fit: cover;
}

@media only screen and (max-width: 600px) {
  .center {
    width: 85%;
  }

  h1 {
    font-size: 26pt;
  }
  h2,
  h3,
  h4 {
    font-size: 20pt;
  }

  li {
    font-size: 18px;
  }
}


